@import url(https://fonts.googleapis.com/css2?family=Lacquer&display=swap);
.tabs{margin-top:45px}.tabs__header{border-bottom:1px solid rgba(82,78,78,0.29)}.tabs__button{position:relative;width:70px;border-top:1px solid transparent;border-left:1px solid transparent;border-right:1px solid transparent;border-bottom:none;padding:15px;font-family:Tahoma, sans-serif;font-weight:bold;color:#07f;background-color:#f7f8f8;cursor:pointer}.tabs__button--active{border-top-color:rgba(82,78,78,0.29);border-left-color:rgba(82,78,78,0.29);border-right-color:rgba(82,78,78,0.29)}.tabs__button--active::after{content:"";position:absolute;bottom:-1px;left:0;display:block;width:68px;height:1px;background-color:#f7f8f8}.tabs__button:active,.tabs__button:focus{outline:none}.tabs__viewport{padding:20px 5px 15px;background-color:#f7f8f8}

*{box-sizing:border-box;margin:0;padding:0}html{font-family:"Lacquer", cursive;font-size:28px;color:#07f}.main{background-image:linear-gradient(to top, #dad4ec 0%, #dad4ec 1%, #f3e7e9 100%);height:100vh;display:flex;flex-direction:column;align-items:center;padding-top:20px}

